import InfeksiOff from "../components/InfeksiOff";
import NavigationBar from "../components/NavigationBar";
import Advice from "../components/Advice";
import ssi from "../assets/lucu/ssi.png";

const MedicationEr = () => {
  return (
    <div className=" mt-2 pt-5 h-auto">
      <NavigationBar logo="Medication Error" />

      <Advice
        id="definisi"
        off="show"
        warna="secondary"
        judul=" Apa itu Insiden ?"
        display="d-none"
        displayimg2="d-none"
        displayimg3="d-none"
        poin1="Insiden report (laporan insiden) adalah dokumen atau catatan resmi yang digunakan untuk melaporkan dan mendeskripsikan kejadian tidak biasa, gangguan, atau peristiwa yang berpotensi merugikan"
      />
      <Advice
        id="satu"
        warna="warning"
        judul="Salah Meresepkan"
        rata="mx-5"
        display="d-none"
        poin1=" Salah menterjemahkan instruksi pemberian obat"
        poin2=" Salah menuliskan resep obat"
      />
      <Advice
        id="dua"
        warna="warning"
        judul="Salah pemberian"
        rata="mx-5"
        display="d-none"
        poin1=" Salah pasien"
        poin2=" Salah waktu : (Terlambat/Cepat)"
        poin3=" Salah cara pemberian/ route : Oral/IV/IM/Epidural"
        poin4=" Salah dosis : Dosis kurang/berlebih"
        poin5=" Salah obat"
        poin6=" Obat stop tetap dilanjutkan"
        poin7=" Obat diberikan tanpa ada resep dokter"
        poin8=" Obat tidak diberikan"
        poin9=" Salah frekuensi"
        poin10=" Obat kosong saat dibutuhkan sampai harus meminjam ke RS lain"
      />

      <Advice
        id="tiga"
        warna="warning"
        judul="Salah pengiriman obat dari farmacy"
        rata="mx-5"
        display=" d-none"
        poin1=" Salah obat"
        poin2=" Salah dosis/ kekuatan obat"
        poin3=" Salah label"
        poin4=" Salah jenis obat"
        poin5=" Obat tertinggal"
        poin6=" Terlambat pengantaran"
      />
    </div>
  );
};

export default MedicationEr;
