import React, { useState } from "react";
import bidan from "../assets/falllll/1.png";
import bidann from "../assets/falllll/2.png";

function FormCheckBox({
  subJudul1,
  subJudul2,
  subJudul3,
  subJudul4,
  subJudul5,
  subJudul6,
  subJudul7,
  subJudul8,
  subJudul9,
  subJudul10,
}) {
  const [selectedValues, setSelectedValues] = useState([]);

  const handleCheckboxChange = (e) => {
    const value = Number(e.target.value); // pakai Number agar bisa dijumlahkan
    const isChecked = e.target.checked;

    if (isChecked) {
      setSelectedValues([...selectedValues, value]);
    } else {
      // jika tidak di ceklist tolong dihapus
      setSelectedValues(selectedValues.filter((v) => v !== value));
    }
  };
  // Total nilai dari checkbox yang dicentang
  const total = selectedValues.reduce((sum, v) => sum + v, 0);
  return (
    <form className="container border border-warning card mx-auto ">
      <h6 className="mb-4 mt-3 ">Antepartum/ Intrapartum</h6>
      <label className="fs-6 fw-light">
        <input type="checkbox" value="2" onChange={handleCheckboxChange} />
        {subJudul1}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="3" onChange={handleCheckboxChange} />
        {subJudul2}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="1" onChange={handleCheckboxChange} />
        {subJudul3}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="1" onChange={handleCheckboxChange} />
        {subJudul4}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="2" onChange={handleCheckboxChange} />
        {subJudul5}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="1" onChange={handleCheckboxChange} />
        {subJudul6}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="1" onChange={handleCheckboxChange} />
        {subJudul7}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="1" onChange={handleCheckboxChange} />
        {subJudul8}
      </label>
      <br />
      <label className="fs-6 fw-light">
        <input type="checkbox" value="2" onChange={handleCheckboxChange} />
        {subJudul9}
      </label>
      <br />
      <label className="fs-6 fw-light mb-4">
        <input type="checkbox" value="3" onChange={handleCheckboxChange} />
        {subJudul10}
      </label>

      <div className="mb-4 text-center">
        <strong>Jumlah skor:</strong> {total} <br />
      </div>
      <btn
        data-bs-toggle="modal"
        data-bs-target="#exampleModal4"
        className="btn btn-primary w-50 mx-auto mb-2"
      >
        advice
      </btn>

      {/* moda box */}
      <div
        class="modal fade"
        id="exampleModal4"
        tabindex="-1"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="modal-header">
              <h1 class="modal-title fs-5" id="exampleModalLabel">
                Advice
              </h1>
              <button
                type="button"
                class="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              ></button>
            </div>
            <div class="modal-body">
              {/* nav */}
              <ul
                class="nav nav-tabs mx-auto text-center"
                id="myTab"
                role="tablist"
              >
                <li class="nav-item" role="presentation">
                  <button
                    class="nav-link active"
                    id="home-tab"
                    data-bs-toggle="tab"
                    data-bs-target="#home-tab-pane"
                    type="button"
                    role="tab"
                    aria-controls="home-tab-pane"
                    aria-selected="true"
                  >
                    Rendah
                  </button>
                </li>
                <li class="nav-item" role="presentation">
                  <button
                    class="nav-link"
                    id="profile-tab"
                    data-bs-toggle="tab"
                    data-bs-target="#profile-tab-pane"
                    type="button"
                    role="tab"
                    aria-controls="profile-tab-pane"
                    aria-selected="false"
                  >
                    Tinggi
                  </button>
                </li>
              </ul>
              <div class="tab-content" id="myTabContent">
                {/* card */}
                <div
                  class="tab-pane fade show active"
                  id="home-tab-pane"
                  role="tabpanel"
                  aria-labelledby="home-tab"
                  tabindex="0"
                >
                  <div class="mx-auto mt-4" style={{ width: "18rem" }}>
                    <img src={bidan} class="card-img-top" alt="..." />
                  </div>
                </div>
                {/* end card */}
                <div
                  class="tab-pane fade"
                  id="profile-tab-pane"
                  role="tabpanel"
                  aria-labelledby="profile-tab"
                  tabindex="0"
                >
                  {/* card */}
                  <div
                    class="tab-pane fade show active"
                    id="home-tab-pane"
                    role="tabpanel"
                    aria-labelledby="home-tab"
                    tabindex="0"
                  >
                    <div class="mx-auto mt-4" style={{ width: "18rem" }}>
                      <img src={bidann} class="card-img-top" alt="..." />
                    </div>
                  </div>
                  {/* end card */}
                </div>
              </div>
              {/* end nav */}
            </div>
            <div class="modal-footer">
              <button
                type="button"
                class="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Close
              </button>
              {/* <button type="button" class="btn btn-primary">
                Save changes
              </button> */}
            </div>
          </div>
        </div>
      </div>
    </form>
  );
}

export default FormCheckBox;
